<template>
    <div class="container">
        <div class="autorize">
            <div class="form_block">
                <form v-on:submit.prevent="register">
                    <h1>Регистрация</h1>
                    <p v-if="result_err"> ОШИБКА РЕГИСТРАЦИИ: {{ errData }}</p>
                    <div class="input_block">
                        <input required type="login" id="username" placeholder="введите имя" v-model="username">
                    </div>
                    <div class="input_block">
                        <input required :type="!show_pass ? 'password' : 'text'" id="password" placeholder="введите пароль" v-model="password">
                    </div>
                    <div class="input_block" v-if="confirm">
                    <input required :type="!show_pass ? 'password' : 'text'" id="confirm_password" placeholder="подтвердите пароль" v-model="confirmed_password">
                    </div>
                    <div class="check_block">
                        <div class="show_pass">
                            <input type="checkbox" id="show_pass" v-model="show_pass">
                            <label for="show_pass">Показать пароль</label>
                        </div>
                    </div>
                    <div class="button_block">
                        <button type="submit" :disabled="!validate" :class="validate ? 'activ' : 'non_activ'">Зарегистрироваться</button>
                        <span><RouterLink to="login">на страницу авторизации</RouterLink></span>
                    </div>
                </form>
            </div>
        </div>
    </div>
</template>

<script>

import axios from 'axios'

export default{
    props:{
        server_addr: String
    }, 
    data(){
        return {
            username: "",
            password: "",
            confirmed_password: "",
            show_pass: false,
            result_err: false,
            errData:""
        }
    },
    methods:{
        register: function(){
            axios.defaults.withCredentials = true
            axios.post(this.server_addr + '/register',{'username':this.username, 'password':this.password})
                .then(() => {
                    this.$router.push('/chat')
                })
                .catch((err) => {
                    this.result_err = true
                    this.errData = err.response.data
                    console.log(this.errData)
                }) 
        }
    },
    computed:{
        confirm(){
            if (this.password.length > 4){
                return true
            } else {
                return false
            }
        },
        validate(){
            let result = true
            if (this.password != this.confirmed_password){
                result = false
            }
            if (this.password.length < 4){
                result = false
            }
            return result
        }
    }
}
</script>

